<template>
  <div
    class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center"
    id="page-login"
  >
    <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
      <vx-card>
        <div slot="no-body" class="full-page-bg-color">
          <div class="vx-row no-gutter justify-center items-center">
            <div class="vx-col hidden lg:block lg:w-1/2">
              <img src="@/assets/images/pages/login.png" alt="login" class="mx-auto w-full" />
            </div>

            <div class="vx-col sm:w-full md:w-full lg:w-1/2 d-theme-dark-bg">
              <div class="p-8 login-tabs-container">
                <div class="vx-card__title mb-12">
                  <h4 class="mb-5 font-bold">Login</h4>
                </div>
                <form>
                  <div>
                    <vs-input
                      name="email"
                      v-validate="'required'"
                      v-model="email"
                      icon-no-border
                      icon="icon icon-user"
                      icon-pack="feather"
                      label-placeholder="Email"
                      class="w-full"
                    />
                    <span
                      class="text-danger text-sm"
                      v-show="errors.has('email')"
                    >{{ errors.first('email') }}</span>

                    <vs-input
                      type="password"
                      v-validate="'required'"
                      name="password"
                      v-model="password"
                      icon-no-border
                      icon="icon icon-lock"
                      icon-pack="feather"
                      label-placeholder="Password"
                      class="w-full mt-8"
                    />
                    <span
                      class="text-danger text-sm"
                      v-show="errors.has('password')"
                    >{{ errors.first('password') }}</span>
                    <vs-button @click.prevent="submitForm" class="block mt-6 w-full">Login</vs-button>

                    <div class="my-5">
                      Can't Login
                      <router-link to class="small">Contact Support?</router-link>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </vx-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    submitForm() {
      this.$vs.loading();
      this.$validator.validateAll().then(result => {
        if (result) {
          // if form have no errors
          let user = {
            email: this.email,
            password: this.password
          };
          this.$store
            .dispatch("login", user)
            .then(resp => {
              // console.log("Worked");

              this.$vs.loading.close();
              this.$router.push("/");
              this.$vs.notify({
                title: "Admin Login",
                text: "Login Successful!",
                color: "success"
              });
            })
            .catch(err => {
              // console.log("Failed");
              this.$vs.loading.close();
              if (err.response) {
                if (err.response.status == 400) {
                  console.log(err.response.data);
                  this.$vs.notify({
                    title: "Admin Login",
                    text: err.response.data,
                    color: "warning"
                  });
                } else {
                  this.$vs.notify({
                    title: "Admin Login",
                    text: "Unable to login, try again!",
                    color: "warning"
                  });
                }
              } else {
                this.$vs.notify({
                  title: "Admin Login",
                  text: "Unable to login, try again!",
                  color: "warning"
                });
              }
            });
        } else {
          // form have errors
        }
      });
    }
  }
};
</script>

